.hotels {
  width: 100%;
  padding-bottom: 120px;
}

.hotels .body {
  background: #fff;
}

.hotels .body_block {
  width: 100%;
  min-height: 50px;
  border-radius: 3px;
  background: var(--unnamed, #fdfdfd);
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
  transition: 0.2s ease;
  margin-bottom: 12px;
}

.hotels .body_block .img {
  border-radius: 3px 3px 0 0 !important;
  width: 100% !important;
  height: 180px !important;
  object-fit: cover !important;
  object-position: center !important;
}

.hotels .content_home {
  padding: 8px;
}

.hotels .content {
  width: 100%;
}

.hotels .conntent_block_one .icon {
  color: gold;
  margin-right: 3px;
}

.hotels .conntent_block_one h1 {
  color: var(--unnamed, #081f2c);
  font-size: 18px;
  font-weight: 600;
}

.hotels .conntent_block_two {
  display: grid;
  grid-template-columns: 3.5fr 0.5fr;
  gap: 8px;
  margin-bottom: 8px;
}

.hotels .conntent_block_two .rate {
  padding: 5px 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  font-size: 15px;
  font-weight: 500;
  border-radius: 3px;
  background: var(--blue);
}

.hotels .conntent_block_two img {
  display: flex;
  margin-left: auto;
  width: 40px;
}

.hotels .body_block p {
  color: var(--unnamed, #081f2c);
  font-size: 14px;
  font-weight: 300;
}

.hotels .beach_div {
  margin-top: 8px;
  display: flex;
  flex-wrap: wrap;
}

.hotels .beach_div .beach {
  display: flex;
  align-items: center;
  margin-right: 7px;
}

.hotels .beach_div .beach img {
  margin-right: 7px;
}

.hotels .conntent_block_two p {
  text-align: end;
}

.hotels .hr {
  width: 100%;
  height: 1px;
  background: rgba(0, 0, 0, 0.1);
  margin: 15px 0;
}

.hotels .price {
  display: flex;
  justify-content: space-between;
  margin-bottom: 7px;
}

.hotels .price h1 {
  text-align: end;
  color: var(--unnamed, #081f2c);
  font-size: 18px;
  font-weight: 600;
}

.hotels .top {
  font-size: 14px;
  line-height: 1;
  height: 43px;
  overflow: hidden;
}

.hotels .not_found {
  width: 100%;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hotels .not_found h1 {
  font-size: 20px;
  font-weight: 500;
  color: #081f2c;
}

.hotels .content_p p {
  text-align: start;
}

.hotels .input {
  margin-top: 12px;
  width: 100%;
  padding: 10px 12px;
  padding-left: 37px !important;
  margin-bottom: 12px;
  font-size: 15px;
  font-weight: 400;
  border-radius: 3px;
  border: 1px solid var(--blue);
}

.hotels .input_box_country {
  position: relative;
}

.hotels .input_box_country .icon_search {
  position: absolute;
  top: 18px;
  left: 8px;
}