.favorites {
  width: 100%;
  min-height: 400px;
  background: #fff;
  padding-bottom: 120px;
}

.favorites .bahr .bahr_img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
}

.favorites .bahr h1 {
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  margin-bottom: 6px;
}

.favorites .bahr {
  display: flex;
  padding: 8px;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
}

.favorites .bahr_all {
  cursor: pointer;
  border-radius: 10px;
  background: var(--white);
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
  transition: 0.2s ease;
}

.favorites .bahr .icon {
  color: #eeb34f;
}

.favorites .bahr .deletes {
  margin-left: auto;
}

.favorites .lin {
  height: 1px;
  margin: 0 auto;
  background: #ddd;
  margin-top: 5px;
  margin-bottom: 5px;
}

.favorites .claen {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 9px;
}

.favorites .clean_div {
  width: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.favorites .sena {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px;
  padding-top: 0;
}

.favorites .bahr_all p {
  color: #111;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
}

.favorites .not_data {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60px;
  text-align: center;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
}

.favorites .not_data img {
  width: 50px;
}

.favorites .tour-info-column {
  display: block;
  margin-top: 8px;
}