.bonus_history {
  width: 100%;
  padding-bottom: 120px;
}

.bonus_history .not_data {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60px;
  text-align: center;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
}

.bonus_history .not_data img {
  width: 50px;
}

.bonus_history .bonus_block_box {
  margin-top: 12px;
  padding: 8px;
  border-radius: 3px;
  background: var(--unnamed, #fdfdfd);
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
}

.bonus_history .bonus_block_box .between {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.bonus_history .bonus_block_box h1 {
  font-size: 16px;
  font-weight: 500;
  color: var(--black);
}

.bonus_history .bonus_block_box h2 {
  font-size: 15px;
  font-weight: 500;
}

.bonus_history .bonus_block_box p {
  margin-top: 8px;
  font-size: 14px;
  font-weight: 400;
  color: #a1a1a1;
}

.bonus_history .bonus_block_box span {
  font-weight: 400;
  color: var(--blue);
}