.city_and_country .img_loader {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  background: var(--blue);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99;
}

.city_and_country .img_loader img {
  max-width: 250px;
  width: 100%;
}

.city_and_country .loading_div.d {
  margin: 0;
}

.city_and_country {
  width: 100%;
  padding-bottom: 120px;
}

.city_and_country .body {
  background: #fff;
  padding-top: 12px;
}

.city_and_country .body_block {
  cursor: pointer;
  width: 100%;
  min-height: 50px;
  border-radius: 3px;
  background: var(--unnamed, #fdfdfd);
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
  transition: 0.2s ease;
  margin-bottom: 12px;
}

.city_and_country .body_block .img {
  border-radius: 3px 3px 0 0 !important;
  width: 100% !important;
  height: 180px !important;
  object-fit: cover !important;
  object-position: center !important;
}

.city_and_country .content_home {
  padding: 8px;
}

.city_and_country .content {
  width: 100%;
}

.city_and_country .conntent_block_one .icon {
  color: gold;
  margin-right: 3px;
}

.city_and_country .conntent_block_one h1 {
  color: var(--unnamed, #081f2c);
  font-size: 18px;
  font-weight: 600;
}

.city_and_country .conntent_block_two {
  display: grid;
  grid-template-columns: 3.5fr 0.5fr;
  gap: 8px;
  margin-bottom: 8px;
}

.city_and_country .conntent_block_two .rate {
  padding: 5px 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  font-size: 15px;
  font-weight: 500;
  border-radius: 3px;
  background: var(--blue);
}

.city_and_country .conntent_block_two img {
  display: flex;
  margin-left: auto;
  width: 40px;
}

.city_and_country .body_block p {
  color: var(--unnamed, #081f2c);
  font-size: 14px;
  line-height: 1;
  font-weight: 300;
}

.city_and_country .beach_div {
  margin-top: 8px;
  display: flex;
  flex-wrap: wrap;
}

.city_and_country .beach_div .beach {
  display: flex;
  align-items: center;
  margin-right: 7px;
}

.city_and_country .beach_div .beach img {
  margin-right: 7px;
}

.city_and_country .conntent_block_two p {
  text-align: end;
}

.city_and_country .hr {
  width: 100%;
  height: 1px;
  background: rgba(0, 0, 0, 0.05);
  margin: 10px 0;
}

.city_and_country .price {
  display: flex;
  justify-content: space-between;
  margin-bottom: 7px;
}

.city_and_country .price h1 {
  text-align: end;
  color: var(--unnamed, #081f2c);
  font-size: 18px;
  font-weight: 600;
}

.city_and_country .content_p p {
  text-align: start;
}

.city_and_country .top {
  font-size: 14px;
  height: 43px;
  overflow: hidden;
}

.city_and_country .not_found {
  width: 100%;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.city_and_country .not_found h1 {
  font-size: 20px;
  font-weight: 500;
  color: #081f2c;
}
