.toolbar {
  left: 10px;
  bottom: 10px;
  right: 10px;
  position: fixed;
  display: flex;
  border-radius: 100px;
  align-items: center;
  padding: 10px 0;
  background: linear-gradient(135deg, #FFFFFF 0%, #F2F2F2 100%), #FFFFFF;
  border: 1px solid #FFFFFF;
  border-radius: 100px;
  box-shadow: 0px 4px 41px rgba(0, 0, 0, 0.18);
  z-index: 1;
}
.ios .toolbar {
  padding-bottom: env(safe-area-inset-bottom) !important;
}
.toolbar .tour_toolbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.toolbar .tour_toolbar .icon_div {
  width: 25%;
  display: flex;
  gap: 2px;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
  border-radius: 15px;
}
.toolbar .tour_toolbar .icon_div .icon {
  color: var(--blue);
}
.toolbar .tour_toolbar .icon_div p {
  margin-top: 4px;
  color: var(--black);
  font-size: 13px;
  font-weight: 500;
  line-height: 1;
}
