.nav {
  width: 100%;
  min-height: 200px;
  padding-bottom: 8px;
  background: url("../../img/aerial\ \(5\)\ \(1\).jpg") no-repeat center / cover;
}

.nav .nav_box {
  margin-top: 105px;
  width: 100%;
  height: 35px;
  overflow-y: hidden;
  border-radius: 100px;
  background: rgba(0, 0, 0, 0.14);
  backdrop-filter: blur(2px);
  display: flex;
  justify-content: space-between;
}

.nav .nav_box::-webkit-scrollbar {
  display: none;
}

.nav .nav_box .nav_link {
  padding: 0 20px;
  height: 35px;
  color: var(--unnamed, #fdfdfd);
  font-size: 14px;
  font-weight: 400;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
}

.nav .nav_box .nav_link.active {
  color: #000;
  font-weight: 500;
  background: #fff;
  border-radius: 300px;
}

.nav .search_block {
  margin-top: 15px;
  width: 100%;
  min-height: 10px;
  border-radius: 3px;
  padding: 15px;
  background: var(--unnamed, #fdfdfd);
  display: grid;
  grid-template-columns: 1fr 20px;
  border: none !important;
}

.nav .search_block .br {
  width: 100%;
  height: 1px;
  background: rgba(0, 0, 0, 0.1);
  margin: 8px 0;
  opacity: 0.5;
}

.nav .search_block p {
  color: var(--unnamed, #026ed2);
  font-size: 14px;
  font-weight: 400;
  display: flex;
  align-items: center;
}

.nav .search_block h1 {
  margin-top: 2px;
  color: var(--unnamed, #081f2c);
  font-size: 16px;
  font-weight: 500;
}

.nav .search_block .icon {
  margin-right: 5px;
}

.nav .image {
  width: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav .filter_block {
  margin-top: 8px;
  width: 100%;
  height: 35px;
  display: grid;
  grid-template-columns: 1.5fr 1fr 1.5fr;
  gap: 8px;
}

.nav .filter_block .icon_btn {
  margin-right: 3px;
  color: var(--unnamed, #026ed2);
}

.nav .filter_block .btn {
  gap: 5px;
  padding: 0 8px;
  border-radius: 3px;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--unnamed, #026ed2);
  font-size: 14px;
  font-weight: 400;
  background: var(--unnamed, #fdfdfd);
}

.nav .filter_block .btn.g {
  font-weight: 600;
}

.nav .search_div {
  width: 100%;
  height: 400px;
}

.nav .air_div {
  margin-top: 12px;
  width: 100%;
  min-height: 50px;
  padding: 8px;
  border-radius: 15px;
  background: var(--unnamed, #026ed2);
}

.nav .air_div p {
  color: var(--unnamed, #fdfdfd);
  font-size: 14px !important;
  font-weight: 500 !important;
}

.nav .all_star {
  width: 100%;
}