.profile {
  width: 100%;
  min-height: 100px;
  background: #026ed2;
  padding: 24px 0 24px 0;
}

.profile .block_area {
  width: 100%;
  min-height: 0;
  border-radius: 15px;
  background: #fdfdfd;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.profile .block_area .avatar {
  width: 100px;
  height: 100px;
  border-radius: 200px;
  background: #026ed2;
  margin: 0 0 10px 0;
}

.profile .block_area h1 {
  color: #081f2c;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: -0.24px;
  margin: 0 0 10px 0;
}

.profile .block_area p {
  color: #081f2c;
  text-align: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 20px;
  letter-spacing: -0.24px;
  margin: 0 0 10px 0;
}

.profile .block_area .button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 10px 0;
  width: 266px;
  height: 42px;
  border-radius: 3px;
  color: #026ed2;
  border: 0.3px solid #026ed2;
  background: rgba(2, 110, 210, 0.21);
}

.profile .block_area .button a {
  color: #026ed2;
  text-align: center;
  font-style: normal;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
}
.block_read {
  width: 100%;
}
.block_read .link_page {
  cursor: pointer;
  color: var(--black);
  font-size: 14px;
  font-weight: 500;
  width: 100%;
  padding: 12px 0;
  display: flex;
  align-items: center;
  background: var(--white);
  border-bottom: 1px solid #ddd;
  position: relative;
}
.block_read .link_page .icon_leng {
  margin-right: 8px;
}
.block_read .link_page img {
  margin-right: 5px;
}
.block_read .icon {
  margin-left: auto;
}
.block_read .link_page .leng {
  margin-left: auto;
  display: flex;
  align-items: center;
}
.leng_text {
  width: 100%;
  padding-bottom: 30px;
}
.leng_text h2 {
  color: var(--black);
  font-size: 14px;
  font-weight: 500;
}
.leng_giv {
  width: 100%;
  padding: 10px 15px;
  display: flex;
  align-items: center;
  cursor: pointer;
  background: var(--white);
  border-bottom: 1px solid #ddd;
}
.leng_giv .dot {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--blue);
  margin-right: 10px;
}

@media (max-width: 380px) {
  .block_reat .row {
    width: 345px;
    height: 40px;
  }
}
@media (max-width: 360px) {
  .block_reat .row {
    width: 325px;
    height: 40px;
  }
}
@media (max-width: 330px) {
  .block_reat .row {
    width: 300px;
    height: 40px;
  }
}
@media (max-width: 315px) {
  .profile .block_area .button {
    width: 246px;
    height: 42px;
  }
}
@media (max-width: 305px) {
  .block_reat .row {
    width: 270px;
    height: 40px;
  }
}
@media (max-width: 290px) {
  .profile .block_area .button {
    width: 215px;
    height: 42px;
  }
  .block_reat .row {
    width: 250px;
    height: 40px;
  }
}
