.bus {
    width: 100%;
    padding-bottom: 120px;
}

.bus .filter_bus_div {
    width: 100%;
    height: 35px;
    display: flex;
    align-items: center;
    gap: 8px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.bus .filter_bus_div h4 {
    font-size: 15px;
    font-weight: 400;
    color: var(--black);
}

.bus .tour_block_flex_div {
    height: 35px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.bus .filter_body {
    width: 100%;
    padding-bottom: 45px;
}

.bus .filter_bus {
    margin-top: 12px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
}

.bus .icon {
    color: var(--blue);
}

.bus .filter_bus p {
    color: #444;
    font-size: 13px;
    font-weight: 300;
}

.bus .filter_bus h3 {
    color: var(--black);
    font-size: 15px;
    font-weight: 500;
}

.bus .bus_block {
    margin-top: 12px;
    width: 100%;
    min-height: 50px;
    border-radius: 3px;
    background: var(--unnamed, #fdfdfd);
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
    transition: 0.2s ease;
    padding: 8px;
}

.bus .bus_block h1 {
    margin-top: 8px;
    font-size: 16px;
    font-weight: 500;
    color: var(--black);
}

.bus .bus_block img {
    width: 100%;
    height: 150px;
    border-radius: 3px;
    object-fit: cover;
    object-position: center;
}

.bus .nights {
    font-size: 14px;
    font-weight: 400;
    color: var(--black);
}

.bus .hr {
    width: 100%;
    height: 1px;
    background: rgba(0, 0, 0, 0.1);
    margin: 10px 0;
}

.bus .bus_block .contant_two {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    margin-bottom: 2px;
}

.bus .bus_block .contant_two .div {
    display: flex;
    align-items: center;
}

.bus .bus_block .contant_two .div .btnp {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.bus .bus_block .contant_two .icon {
    margin-right: 10px;
    color: var(--blue);
}

.bus .bus_block .contant_two p {
    color: var(--black);
    font-size: 13px;
    font-weight: 300;
}

.bus .bus_block .contant_two h3 {
    width: 100%;
    color: var(--black);
    font-size: 15px;
    font-weight: 500;
}

.bus .not_data {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
    text-align: center;
    font-size: 14px;
    font-weight: 600;
    text-transform: uppercase;
}

.bus .not_data img {
    width: 50px;
}

.bus .head {
    font-size: 16px;
    font-weight: 500;
    color: var(--black);
    text-align: center;
    margin-bottom: 15px;
}

.bus .user_box_tour .btns h1 {
    justify-content: center;
    margin: 0 !important;
}