.history {
  width: 100%;
  min-height: 400px;
  padding-bottom: 120px;
  background-color: #fff;
}

.history .history_all {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 8px;
  border-radius: 3px;
  background: #fdfdfd;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
  margin-top: 12px;
  cursor: pointer;
}

.history .history_all_one {
  display: flex;
  align-items: center;
  gap: 10px;
}

.history .rixos {
  display: flex;
  flex-direction: column;
  gap: 1px;
}

.history .history_all img {
  width: 65px;
  height: 65px;
  border-radius: 5px;
  object-fit: cover;
  object-position: center;
}

.history .history_all h2 {
  color: var(--black);
  font-size: 16px;
  line-height: 1;
  font-weight: 500;
}

.history .history_all .height {
  height: 32px;
  overflow: hidden;
}

.history .history_all h3 {
  color: var(--black);
  font-size: 13px;
  font-weight: 500;
}

.history .history_all h3 span {
  font-weight: 300;
}

.history .history_all a {
  color: var(--blue);
  font-size: 14px;
  font-weight: 500;
}

.history .status {
  margin-top: 3px;
  font-size: 15px;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 5px;
}

.history .status.blue {
  color: #00c3ff;
}

.history .status.green {
  color: rgb(3, 196, 3);
}

.history .status.yellow {
  color: rgb(255, 200, 0);
}

.history .status.red {
  color: red;
}

.history .not_data {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60px;
  text-align: center;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
}

.history .not_data img {
  width: 50px;
}

.history .hr {
  margin: 8px 0;
  width: 100%;
  height: 1px;
  background: rgba(0, 0, 0, 0.1);
}

.history .dowload {
  width: 100%;
  margin: 0;
  font-size: 15px;
  font-weight: 400;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 8px;
  border-radius: 5px;
  border: none;
}

.history .dowload.doc {
  margin: 3px 0;
  justify-content: center;
}

.history .hostory_doc {
  display: flex;
  align-items: center;
  display: grid;
  grid-template-columns: 5fr 1fr;
}

.history .flex {
  width: 100%;
  display: flex;
  align-items: center;
}

.history .flex p {
  font-size: 14px;
  font-weight: 300;
  color: rgba(0, 0, 0, 0.3);
}

.history .iocn_doc {
  color: var(--blue);
  margin: 0 10px;
}