.reis {
  width: 100%;
  padding-bottom: 120px;
  background: #fff;
}

.reis .img_loader {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  background: var(--blue);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99;
}

.reis .img_loader img {
  max-width: 250px;
  width: 100%;
}

.reis .slide {
  margin: 0 auto;
  max-width: 820px;
}

.reis .slide_border {
  border: none;
}

.reis .slide img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  object-position: center;
}

.reis .icon_bl {
  cursor: pointer;
  color: #026ed2;
}

.reis .category {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.reis .category::-webkit-scrollbar {
  display: none;
}

.reis .category .btn {
  margin-top: 8px;
  margin-right: 8px;
  padding: 6px 12px;
  color: var(--unnamed, #000);
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  border-radius: 3px;
  background-color: transparent;
  border: 1px solid var(--unnamed, #026ed2);
}

.reis .btn.active {
  background: var(--blue);
  color: var(--unnamed, #fdfdfd);
  font-weight: 400;
}

.reis .slick-dots {
  margin-bottom: 35px;
}

.reis .relative {
  position: relative;
}

.reis .icon_blue {
  position: absolute;
  top: -60px;
  left: 0;
  color: var(--blue);
  cursor: pointer;
}

.reis .icon_red {
  font-weight: 500;
  color: #cd2b1e;
  cursor: pointer;
  position: absolute;
  top: -60px;
  right: 0;
}

.reis .head_text {
  margin-top: 12px;
  width: 100%;
  min-height: 50px;
  padding: 8px;
  border-radius: 3px;
  background: var(--unnamed, #fdfdfd);
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
}

.reis .head_text h1 {
  color: var(--unnamed, #081f2c);
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 12px;
}

.reis .head_text p {
  color: var(--unnamed, #081f2c);
  font-size: 14px;
  font-weight: 300;
}

.reis .head_text .stars {
  margin: 5px 0;
}

.reis .head_text .stars .icon {
  color: gold;
  margin-right: 3px;
}

.reis .services {
  margin-top: 12px;
  width: 100%;
  min-height: 50px;
  padding: 8px;
  border-radius: 3px;
  background: var(--unnamed, #fdfdfd);
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
}

.reis .services h1 {
  color: var(--unnamed, #081f2c);
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 8px;
}

.reis .services li {
  margin-left: 20px;
  color: var(--unnamed, #081f2c);
  font-size: 14px;
  font-weight: 300;
}

.reis .book_tour {
  display: flex;
  margin: 0 auto;
  margin-top: 25px;
  color: var(--unnamed, #fdfdfd);
  font-size: 14px;
  font-weight: 500;
  padding: 12px 80px;
  border: none;
  border-radius: 4px;
  background: var(--unnamed, #026ed2);
  box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.1);
}

.reis .book_tour:hover {
  background: var(--dark);
}

.reis .more {
  margin: 0 auto;
  margin-top: 30px;
  padding: 5px 15px;
  font-size: 14px;
  display: flex;
  align-items: center;
  border: none;
  background: transparent;
}

.reis .more .icon {
  margin-left: 10px;
}

.reis .reviews .review_block {
  margin-top: 15px;
  width: 100%;
  min-height: 0px;
  padding: 8px;
  border-radius: 3px;
  background: var(--white);
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
}

.reis .reviews .review_block_box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.reis .reviews .emoji p {
  margin: 3px 0 !important;
}

.reis .reviews .review_block h1 {
  font-size: 16px;
  color: var(--black);
  font-weight: 500;
  margin-bottom: 5px;
}

.reis .reviews .review_block h2 {
  margin-top: 12px;
  font-size: 15px;
  color: var(--black);
  font-weight: 500;
}

.reis .reviews .review_block p {
  margin-top: 8px;
  font-size: 15px;
  line-height: 1;
  color: var(--black);
  font-weight: 400;
}

.reis .review_block .read {
  text-align: end;
  font-weight: 500 !important;
  cursor: pointer;
  margin-top: 5px !important;
}

.reis .reviews .icon {
  color: gold;
  margin-left: 3px;
}

.reis .reviews .date_review {
  margin-top: 15px;
  display: flex;
  justify-content: flex-end;
}

.reis .reviews .date_review p {
  font-weight: 400;
  color: var(--blue);
  margin: 0 5px 0 0;
}

.reis .map {
  margin-top: 20px;
}

.reis .icon_div {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 8px;
}

.reis .tour_details {
  width: 100%;
  padding-top: 20px;
}

.reis .tour_details .tour_box {
  cursor: pointer;
  margin-top: 12px;
  width: 100%;
  padding: 8px;
  border-radius: 3px;
  background: var(--white);
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
}

.reis .tour_details .tour_box .contant_one {
  display: flex;
  align-items: center;
  width: 100%;
}

.reis .tour_details .tour_box .contant_one p span {
  color: var(--black);
  font-size: 16px;
  font-weight: 500;
}

.reis .tour_details .tour_box .contant_two {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.reis .tour_details .tour_box .contant_two .div {
  margin-top: 12px;
  display: flex;
  align-items: center;
}

.reis .tour_details .tour_box .contant_two .div .btnp {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.reis .tour_details .tour_box .contant_two .icon {
  margin-right: 10px;
  color: var(--blue);
}

.reis .tour_details .tour_box .contant_two p {
  color: var(--black);
  font-size: 13px;
  font-weight: 300;
}

.reis .tour_details .tour_box .contant_two h3 {
  width: 100%;
  color: var(--black);
  font-size: 15px;
  font-weight: 500;
}

.reis .tour_details .tour_box h1 {
  width: 90%;
  color: var(--black);
  font-size: 16px;
  font-weight: 500;
}

.reis .tour_details .tour_box p {
  color: var(--black);
  font-size: 14px;
  font-weight: 300;
}

.reis .tour_details .btn_tour {
  color: var(--white);
  font-size: 15px;
  font-weight: 500;
  padding: 3px 12px;
  border-radius: 10px;
  background: var(--blue);
  border: none;
  display: flex;
  margin: auto auto 0 auto;
}

.reis .tour_details .price {
  font-size: 15px !important;
  font-weight: 600 !important;
  margin: 5px auto;
  text-align: center;
}

.reis .reviewscount {
  margin-top: 25px;
  text-align: center;
}

.reis .hr {
  width: 100%;
  height: 1px;
  margin: 10px 0 0 0;
  background: rgba(0, 0, 0, 0.05);
}

.reis .gallery {
  margin-top: 12px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
}

.reis .gallery img {
  width: 100%;
  height: 20vh;
  object-fit: cover;
  object-position: center;
  border-radius: 8px;
}