.modal_home {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  background: rgb(0, 0, 0, 0.5);
  z-index: 999;
  animation: modalOpen 0.4s ease;
}
@keyframes modalOpen {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.modal_home .not_modal {
  width: 100%;
  height: 100vh;
  position: relative;
}
.modal_home .modal {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  min-height: 100px;
  max-height: 500px;
  background: #fff;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  z-index: 1;
  animation: modalTop 0.3s ease;
}
@keyframes modalTop {
  0% {
    min-height: 0;
    max-height: 0;
  }
  100% {
    min-height: 100px;
    max-height: 500px;
  }
}
.modal_home .padding::-webkit-scrollbar {
  display: block;
}
.modal_home .padding {
  padding-top: 15px;
  background: transparent;
}
