.version {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    z-index: 99999;
    display: flex;
    align-items: center;
    justify-content: center;
}

.verson_block {
    width: 80%;
    min-height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
    border-radius: 3px;
    padding: 25px 15px 15px 15px;
    background: #fdfdfd;
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
}

.up_date {
    margin: 15px 0 30px 0;
    font-size: 15px;
    font-weight: 500;
    color: #777;
}

.verson_block h1 {
    margin: 15px 0;
    color: #000;
    font-size: 16px;
    font-weight: 400;
}

.verson_block .dowload {
