    height: 40px;
    background: #fdfdfd;
    border: none;
    outline: none;
    font-size: 15px;
    font-weight: 400;
    color: #081f2c;
    border: 1px solid var(--blue);
    border-radius: 3px;
    padding: 0 8px;
}

.input_form:focus {
    border: 1px solid #025aac;
}

.input_select {
    margin-top: 5px;
