    outline: none;
    color: #081f2c;
    background: #fdfdfd;
    border: 1px solid var(--blue);
    border-radius: 3px;
    padding: 0 4px;
}

.button_form {
    margin-top: 8px;
    outline: none;
    border: none;
    width: 100%;
    height: 40px;
    border-radius: 3px;
    background: #026ed2;
    box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.1);
    color: #fdfdfd;
    font-size: 14px;
    font-weight: 400;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    cursor: pointer;
}

.input_box {
    width: 100%;
    height: 75px;
    position: relative;
}

.span-icon {
    z-index: 1;
    position: absolute;
    top: 30px;
    right: 8px;
    font-size: 20px;
    color: #026ed2;
    cursor: pointer;
}

.container_block {
    max-width: 780px;
    width: 90%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    gap: 12px;
}

.tour_block {
    margin-top: 12px;
