.activation {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 12px;
}
.activation form {
  width: 100%;
}
.activation p {
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  color: var(--black);
}
.activation .re_send {
  margin-top: 8px;
  font-size: 14px;
  font-weight: 500;
  color: var(--blue);
  text-align: center;
  cursor: pointer;
}
